@use "@utils/variables" as v;

.wrapper.openModal {
    height: 90vh;
    overflow-y: hidden;
}

img {
    object-fit: cover;
}

.wrapper {
    .banner {
        display: flex;
        padding-block: 1em;
        background-color: #abc9e7;
        width: 100%;

        background-image: url("../../assets/products/banner_bg.svg");
        background-position: center;
        background-size: cover;

        position: relative;
        display: flex;
        justify-content: center;

        .underlay {
            position: absolute;
            left: 50%;
            top: 70px;
            width: 80%;
            height: 60%;
            transform: translateX(-50%);
            background-color: black;
            opacity: 0.1;
            border-radius: 20px;
        }
    }

    .productsSection {
        padding-top: 2em;
        header {
            display: flex;
            margin-bottom: 5rem;
            align-items: center;
        }

        .products {
            .prodRow {
                display: flex;
                width: 100%;

                article {
                    $bRadius: 40px;
                    min-width: 250px;
                    max-width: 250px;
                    border-radius: $bRadius;
                    margin-right: 4em;
                    margin-bottom: 5em;
                    box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.2);

                    .productImage {
                        height: 150px;

                        img {
                            width: 100%;
                            height: 100%;
                            border-top-right-radius: $bRadius;
                            border-top-left-radius: $bRadius;
                        }

                        position: relative;
                        .rating {
                            position: absolute;
                            right: 10px;
                            bottom: 10px;
                            border-radius: 20px;
                            background-color: #cdf3fb;
                            width: 45px;
                            height: 22px;
                            border-radius: 20px;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            padding-inline: 7px;
                            box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.5);
                            font-size: 0.8em;

                            svg {
                                color: #f9e319;
                            }
                        }
                    }

                    .productInfo {
                        padding: 1em;

                        .name {
                            margin-bottom: 0.3em;
                        }

                        .category {
                            color: rgba(1, 1, 1, 0.5);
                            font-size: 0.9rem;
                            margin-bottom: 0.5em;
                        }

                        .price {
                            font-weight: 500;
                        }

                        .buttons {
                            display: flex;
                            flex-direction: column;
                            padding-top: 1em;

                            $btnHoverColor: #c007af;

                            button {
                                height: 30px;
                                width: 60%;
                                transition: all 0.3s;
                            }

                            button:first-child {
                                color: white;
                                border: none;
                                margin-bottom: 1em;
                            }
                            button:first-child:hover {
                                background-color: $btnHoverColor;
                                transform: scale(1.05);
                            }
                        }
                    }

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    .wrapper {
        .productsSection {
            .products {
                justify-content: center;
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .wrapper {
        .banner {
            .underlay {
                position: absolute;
                left: 50%;
                top: 70px;
                width: 80%;
                height: 75%;
                transform: translateX(-50%);
                background-color: black;
                opacity: 0.1;
                border-radius: 20px;
            }
        }
        .productsSection {
            .products {
                flex-direction: column;
                align-items: center;

                article {
                    margin: 0;
                    margin-bottom: 2.5em;
                    width: 300px;

                    .productImage {
                        height: auto;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 650px) {
    .wrapper {
        .products {
            .prodRow {
                justify-content: center;
            }
        }
    }
}
