@use "@utils/variables" as v;

.wrapper {
    max-width: 900px;
    max-height: 700px;
    padding: 3em;
    padding-top: 3em !important;

    height: fit-content;
    overflow-y: auto;

    article {
        z-index: 10;
        margin-block: 1em;
        width: 100%;

        .productImage {
            width: 100%;
            border-radius: 20px;
            max-height: 300px;
            img {
                border-radius: 20px;
                z-index: 10;
                width: 100%;
                max-height: 300px;
                object-fit: cover;
            }
        }

        .productInfo {
            width: 100%;
            padding-top: 1em;
            padding-inline: 1.5em;
            height: 100%;

            .name {
                margin-bottom: 0.5em;
                font-size: 1.2rem;
                display: flex;
                align-items: center;

                span {
                    margin-left: 12px;
                    display: inline-block;
                    font-size: 0.8rem;
                    color: rgba(1, 1, 1, 0.5);
                    border-radius: 20px;
                    padding-block: 2px;
                    padding-inline: 10px;
                    border: 1px solid rgba(1, 1, 1, 0.3);
                }
            }

            .price {
                font-weight: 500;
                color: v.$themePurple;
                margin-bottom: 0.5em;
                font-size: 1.2rem;
            }

            .description {
                color: rgba(1, 1, 1, 0.7);
                margin-bottom: 1.5em;
                line-height: 28px;
            }

            $btnHoverColor: #c456ae;
            button {
                height: 35px;
                transition: all 0.3s;
                max-width: 150px;
                color: white;
                border: none;
                margin-top: auto;
            }

            button:hover {
                background-color: $btnHoverColor;
                transform: scale(1.05);
            }
        }
    }
}

@media screen and (max-width: 850px) {
    .wrapper {
        max-width: 95vw;
        padding: 2em;
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        padding: 1em;
    }
}
