.wrapper {
    width: 90%;

    article.slide {
        display: flex;
        width: 100%;
        max-height: 240px;
        border-radius: 20px;

        .productImage {
            width: 50%;
            background-color: white;
            border-radius: 20px;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;

            img {
                width: 100%;
                height: 100%;
                border-radius: 20px;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }
        }

        .productInfo {
            padding: 1em;
            padding-inline: 2.5em;
            width: 50%;
            background-color: white;

            border-radius: 20px;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;

            .ribbon {
                $height: 30px;

                height: $height;
                position: absolute;
                left: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 350px;

                .text {
                    position: relative;
                    width: 100%;
                    text-align: center;
                    line-height: $height;
                    color: white;
                    font-size: 0.9rem;
                }

                .ribbonTop,
                .ribbonBottom {
                    border: 1px solid transparent;
                    position: absolute;
                    top: 0;
                    background: #0099b8;
                    width: 100%;
                    height: calc(#{$height}/ 2 + 1px);
                    clip-path: polygon(0% 0%, 8% 100%, 92% 100%, 100% 0%);
                    z-index: -1;
                }
                div:last-child {
                    transform: rotate(180deg);
                    top: calc(#{$height}/ 2);
                }
            }

            .name {
                padding-top: 3em;
                margin-bottom: 0.3em;
                color: #0099b8;
                font-weight: 600;
                font-size: 1.2em;
            }

            .category {
                color: rgba(1, 1, 1, 0.5);
                font-size: 0.9rem;
                margin-bottom: 0.1em;
            }

            .rating {
                margin-block: 0.3em;
            }

            .price {
                font-weight: 500;
                margin-bottom: 0.2em;
            }

            .buttons {
                display: flex;
                flex-direction: column;
                padding-top: 1em;

                $btnHoverColor: #c456ae;

                button {
                    height: 30px;
                    width: 140px;
                    transition: all 0.3s;
                }

                button:first-child {
                    color: white;
                    border: none;
                    margin-bottom: 1em;
                }
                button:first-child:hover {
                    background-color: $btnHoverColor;
                    transform: scale(1.05);
                }
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .wrapper {
        article.slide {
            flex-direction: column;
            max-height: unset;
            border-radius: 20px !important;

            .productImage {
                width: 100%;
                max-height: 250px !important;
                border-radius: 20px;
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;

                img {
                    border-radius: unset;
                    border-radius: 20px;
                    border-bottom-left-radius: 0;
                    border-bottom-right-radius: 0;
                }
            }

            .productInfo {
                width: 100%;
                border-radius: 20px;
                border-top-right-radius: 0;
                border-top-left-radius: 0;

                .name {
                    padding-top: 0;
                    margin-top: 1em;
                }

                .ribbon {
                    width: 80%;
                    top: 10%;
                }
            }
        }
    }
}
